import axios from 'axios';
import constant from "../../config/constant";

class stepsService {
    static getRecipeSteps(data) {
        const payload = JSON.stringify(data);

        const config = {
            method: 'post',
            url: constant.URL_MASTER_PATH + constant.API_GET_START_COOKING,
            headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`,
                'Content-Type': 'application/json'
            },
            data: payload
        };

        return new Promise((resolve, reject) => {
            axios(config).then(response => {
                resolve(response);
            }).catch(error => {
                reject(error);
            });
        })
    }

    static setStepDone(data, id) {
        const payload = JSON.stringify(data);

        const config = {
            method: 'put',
            url: constant.URL_MASTER_PATH + constant.API_GET_START_COOKING + '/' + id + '/done-step',
            headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`,
                'Content-Type': 'application/json'
            },
            data: payload
        };

        return new Promise((resolve, reject) => {
            axios(config).then(response => {
                resolve(response);
            }).catch(error => {
                reject(error);
            });
        })
    }
}

export default stepsService;